import { instance } from '../../axios';
import './index.css'
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { setCookie } from '../../utils';

export function Login() {
    // const [id, setId] = useState(0);
    const [username, setUsername] = useState(""); //Thêm state username
    const [password, setPassword] = useState(""); //Thêm state password
    let navigate = useNavigate(); // chuyển hướng trang

    const onChangeUsername = (e) => {
        setUsername(e.target.value);
        //console.log(e.target.value);
    };

    const onChangePassword = (e) => {
        setPassword(e.target.value);
        //console.log(e.target.value);
    };

    const handleSignIn = (e) => {
        e.preventDefault(); //Chặn reload page

         //Từ frontend: Mình sẽ gửi về server(backend) 2 thông tin là username và password
        instance
          .post("/common/login", {
            username: username,
            password: password,
          })
        .then((result) => { 
            if(result.data == "success"){                //call API thành công
                setCookie("loginState", result.data, 5); //set Cookie 
                navigate("/");                           //navigate sang trang Home
            }
            else if(result.data == "failed"){ //call API thất bại
                alert("Invalid username or password!");
            }                        
        })
        .catch((error) => { 
            console.log(error);
        });   
    }


    return (
        <div className="bodyLogin">
        <div className="login-wrap">
            <div className="login-html">
            <input
                id="tab-1"
                type="radio"
                name="tab"
                className="sign-in"
                defaultChecked
            />
            <label htmlFor="tab-1" className="tab">
                Sign In
            </label>
            <input id="tab-2" type="radio" name="tab" className="sign-up" />
            <label htmlFor="tab-2" className="tab">
                Sign Up
            </label>
            <div className="login-form">
                {/*SIGN IN */}
                <div className="sign-in-htm">

                {/*Username (của Sign In) */}
                <div className="group">
                    <label htmlFor="user" className="label">
                    Username
                    </label>
                    <input 
                        id="user" 
                        type="text" 
                        required 
                        className="input" 
                        onChange={onChangeUsername}
                        />
                </div>
                {/*---------------------------------------*/}
                {/*Password (của Sign In) */}
                <div className="group">
                    <label htmlFor="pass" className="label">
                    Password
                    </label>
                    <input
                    id="pass"
                    type="password"
                    required
                    className="input"
                    data-type="password"
                    onChange={onChangePassword}
                    />
                </div>
                {/*---------------------------------------*/}
                <div className="group">
                    <input
                    id="check"
                    type="checkbox"
                    className="check"
                    defaultChecked
                    />
                    <label htmlFor="check">
                    <span className="icon" /> Keep me Signed in
                    </label>
                </div>
                {/*---------------------------------------*/}
                {/*Button Sign In */}
                <div className="group">
                    <input
                    type="submit"
                    className="button"
                    defaultValue="Sign In"
                    value="Sign In"
                    onClick={handleSignIn}
                    />
                </div>
                {/*---------------------------------------*/}
                {/*Đường nằm ngang (của Sign In) */}
                <div className="hr" />
                {/*---------------------------------------*/}
                {/*Forgot password (của Sign In)*/}
                <div className="foot-lnk">
                    <a href="#forgot">Forgot Password?</a>
                </div>
                </div>
                {/*____________________________________________________________*/}
                {/*SIGN UP */}
                <div className="sign-up-htm">
                <div className="group">
                    <label htmlFor="user" className="label">
                    Username
                    </label>
                    <input id="user" type="text" className="input" />
                </div>
                <div className="group">
                    <label htmlFor="pass" className="label">
                    Password
                    </label>
                    <input
                    id="pass"
                    type="password"
                    className="input"
                    data-type="password"
                    />
                </div>
                <div className="group">
                    <label htmlFor="pass" className="label">
                    Repeat Password
                    </label>
                    <input
                    id="pass"
                    type="password"
                    className="input"
                    data-type="password"
                    />
                </div>

                <div className="group">
                    <label htmlFor="pass" className="label">
                    Email Address
                    </label>
                    <input id="pass" type="text" className="input" />
                </div>

                <div className="group">
                    <input
                    type="submit"
                    className="button"
                    defaultValue="Sign Up"
                    value="Sign Up"
                    />
                </div>

                <div className="hr" />
                <div className="foot-lnk">
                    <label htmlFor="tab-1">Already Member?</label>
                </div>
                </div>
            </div>
            </div>
        </div>
        </div>
    );
}
